@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Courier+Prime&display=swap);
* {
  font-family: "Roboto", sans-serif;
}

html,
body {
  margin: 0;
  padding: 0;
}

/* Header */

.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
  background-color: #000000dd;
}

.logo {
  position: fixed;
  width: 110px;
  top: 15px;
  left: 50%;
  margin-left: -55px;
  z-index: 50;
}

nav > ul {
  margin: 0 7% 0 7%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  min-height: 75px;
  height: auto;
}

nav > ul > li {
  display: inline;
}

.navbutton {
  margin: 1em;
  color: #d4d4d4;
  font-size: 1.3em;
  text-decoration: none;
  display: inline-block;
  position: relative;
  padding-bottom: 5px;
  border: none;
  background: none;
}

.mobile {
  display: none;
}

.mobile-nav-item {
  width: 100%;
  text-align: center;
  transition: 1s;
}

.mobile-nav-menu {
  position: relative;
  width: 100%;
  top: -500px;
  background-color: #000000dd;
  transition: all 1s;
}

.mobile-nav-menu-wrapper {
  width: 100%;
  height: 250px;
  position: absolute;
  overflow-y: hidden;
  background: none;
  pointer-events: none;
}

.top-nav {
  padding-top: 50px;
}

.nav-active {
  display: block !important;
  top: 0%;
  pointer-events: all !important;
}

.ham {
  position: absolute;
  right: 30px;
  z-index: 100;
}

.mobile-nav-item .navbutton {
  margin: 10px;
}

@media screen and (max-width: 768px) {
  .desktop {
    display: none !important;
  }
  .mobile {
    display: block;
  }
}

@media screen and (max-width: 850px) {
  .desktop-destinations {
    display: none !important;
  }
}

.navbutton:after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: #fff;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.navbutton:hover:after {
  width: 100%;
  left: 0;
}

.navbutton:hover {
  color: #fff;
  cursor: pointer;
}

.logo-spacer {
  width: 110px;
}

/* Footer */

.footer {
  margin-top: 35px;
  background-color: black;
  height: 85px;
  width: 100%;
  color: white;
  font-size: 0.9em;
  display: flex;
  align-items: center;
}

.footer a {
  color: white;
}

.copyright {
  margin-left: 4%;
  width: 100%;
}

.copyright-mobile {
  text-align: center;
  width: 100%;
}

.connect {
  margin-right: 4%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.connect img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.connect img {
  width: 45px;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.connect a {
  padding: 0 15px;
  white-space: nowrap;
  text-decoration: none;
  font-size: 1.5em;
}

.connect a:hover {
  text-decoration: underline;
}

/* Home */
