  font-weight: normal;
  margin-bottom: 0.5em;
}

.subsection {
  margin-top: 1em;
  font-weight: normal;
  margin-bottom: 0.5em;
}

.section-text {
  font-size: 1.3em;
  margin: 0;
  text-align: center;
}

.companies {
  margin-left: 7%;
  margin-right: 7%;
  padding: 0;
}

.company {
  margin-top: 25px;
  display: flex;
}

.company-logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.company-logo img {
  width: 75px;
}

.vert-line {
  flex: 1 1;
  margin-top: 10px;
  width: 2px;
  background-color: #d4d4d4;
}

.company-text {
  margin-left: 20px;
}

.company-sub {
  color: #808080;
  font-size: 0.9em;
}

.company-name {
