}

.position-name {
  margin-top: 10px;
  font-weight: bolder;
  font-size: 1.1em;
}

.objectives {
  font-size: 0.95em;
  list-style-type: none;
}

.objectives li {
  margin-top: 5px;
}

.objectives li:before {
  content: "- ";
}

.skills {
  margin: 25px 7%;
  display: flex;
  flex-direction: column;
}

.skills div {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.skills img {
  padding: 15px 20px;
  width: 50px;
}

.skill:hover .bubble {
  opacity: 1;
}

.skill {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.bubble {
  position: absolute;
  bottom: 100%;
  opacity: 0;
  padding: 0 5px 0 5px;
  text-align: center;
  background: black;
  border-radius: 0.4em;
  min-width: 50px;
  color: black;
}

.bubble:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-top-color: #000000;
  color: black;
  border-bottom: 0;
  margin-left: -10px;
  margin-bottom: -10px;
  text-align: center;
}

.bubble p {
  margin: 3px 5px;
  color: white;
  text-align: center;
  width: 100%;
  white-space: nowrap;
}

.projects {
  margin: 25px 7%;
  display: flex;
  flex-wrap: wrap;
}

.project {
  margin: 15px 20px;
  height: 200px;
  display: block;
  border-width: 2px;
  border-style: solid;
  position: relative;
}

.projects img {
  max-height: 100%;
  max-width: 100%;
}

.project:hover .screen {
  opacity: 1;
}

.screen {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.71);
  opacity: 0;
  color: white;
  text-align: center;
  font-size: 1.5em;
  transition: all 0.5s;
}

.screen:hover {
  cursor: pointer;
}

#experience::before {
  display: block;
  content: " ";
  margin-top: -130px;
  height: 130px;
  visibility: hidden;
  pointer-events: none;
}

/* Aviation */

.aviation {
  min-height: calc(100vh - 120px);
}

.aviation-header {
  margin-top: 150px;
  font-style: italic;
  font-weight: bold;
  font-size: 3em;
  text-align: center;
  background-image: linear-gradient(
    90deg,
    rgba(210, 210, 210, 0) 0%,
    rgba(210, 210, 210, 1) 50%,
    rgba(210, 210, 210, 0) 100%
  );
  margin-left: 7%;
  margin-right: 7%;
}

.aviation-intro {
  margin: 25px 2% 40px 2%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.aviation-about {
  max-width: 600px;
  padding: 20px;
  margin: 0;
  position: relative;
}

.aviation-about p {
  text-indent: 45px;
  line-height: 1.8;
  font-size: 1.1em;
  text-align: justify;
}

.aviation-about img {
  opacity: 0.4;
  position: absolute;
  top: 50%;
  height: 128px;
  margin-top: -64px;
  left: 50%;
  margin-left: -244px;
}

.flags {
  display: flex;
  justify-content: space-evenly;
  margin: auto;
  padding-bottom: 50px;
  padding-left: 15%;
  padding-right: 15%;
  overflow: hidden;
}

.flag {
  -webkit-filter: drop-shadow(0 7px 4px grey);
          filter: drop-shadow(0 7px 4px grey);
  margin-left: 5px;
  margin-right: 5px;
  will-change: filter;
}

.flag img {
  max-width: 70px;
  height: auto;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.flag:hover {
  -webkit-transform: scale(1.3) translateY(11%);
          transform: scale(1.3) translateY(11%);
}

#map {
  background-color: #fafaf8;
  border: 5px solid black;
  height: 400px;
  width: 60%;
  min-width: 300px;
  margin: auto;
}

.map-marker {
  width: 10px;
  height: 10px;
  position: relative;
  left: -5px;
  top: -10px;
}

.aircraft {
  display: flex;
  margin: 25px 7% 0 7%;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.ac {
  text-align: center;
  display: block;
}

.ac img {
  height: 100px;
  opacity: 0.5;
}

.ac-id {
  margin: 15px 0;
  display: block;
  text-align: center;
  font-weight: bolder;
  font-size: 1.8em;
}

.ac-time {
  margin-top: 20px;
  width: 176px;
}

.aviation-section-text {
  display: block;
  text-align: center;
  font-style: italic;
  font-size: 2em;
}

.aviation-subsection-text {
  display: block;
  text-align: center;
  font-style: italic;
  font-size: 1.7em;
}

.hours {
  display: block;
  color: gray;
  text-align: center;
  font-size: 5em;
  margin-bottom: 50px;
}

.ac-hours {
  display: block;
  color: gray;
  text-align: center;
  font-size: 2em;
  margin-bottom: 50px;
}

/* Contact */

.contact-page {
