}

/* Slideshow */

.aviation-slideshow {
  position: relative;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  width: 500px;
  height: 300px;
}

.modal-slideshow {
  margin: 25px 5% 50px 25px;
  position: relative;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  width: 400px;
  height: 200px;
  float: right;
}

.modal-mobile-slideshow {
  margin: 25px 5% 50px 25px;
  position: relative;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  width: 200px;
  height: 300px;
  float: right;
}
.slide {
  text-align: center;
  overflow: hidden;
  height: 100%;
}

.slide img {
  width: 100%;
}

.fade {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

.caption {
  color: white;
  background-color: #00000088;
  font-size: 1em;
  padding: 8px 12px;
  position: absolute;
  top: 45px;
  right: 5px;
  text-align: right;
}

.prev,
.next {
  background-color: #00000088;
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: background-color 0.6s ease;
  border: none;
}

.next {
  right: 5px;
  border-radius: 3px 0 0 3px;
}

.prev:hover,
.next:hover {
  background-color: #000000cc;
}

.dotbar {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  bottom: -25px;
}

.slide-dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active,
.slide-dot:hover {
  background-color: #717171;
}

.destinations {
  width: 60%;
  min-width: 300px;
}

.departure-board {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  text-align: center;
  padding: 0.36em;
  line-height: 1.3em;
  background: rgb(30, 30, 30);
  border-radius: 0 0 0.21em 0.21em;
  color: #eee;
  font-family: Helvetica, sans-serif;
}

.departure-board span.letter {
  display: inline-block;
  width: 1em;
  margin: 0 0.1em;
  height: 1.3em;
  text-align: center;
  position: relative;
}

.departure-board span.letter {
  -o-box-shadow: inset 0 -0.07em 0 rgba(50, 50, 50, 0.7),
    inset 0 -0.14em 0 rgba(0, 0, 0, 0.7),
    inset 0.14em 0 0.28em rgba(0, 0, 0, 0.9),
    inset -0.14em 0 0.28em rgba(0, 0, 0, 0.9),
    0 0.07em 0 rgba(255, 255, 255, 0.2);
  box-shadow: inset 0 -0.07em 0 rgba(50, 50, 50, 0.7),
    inset 0 -0.14em 0 rgba(0, 0, 0, 0.7),
    inset 0.14em 0 0.28em rgba(0, 0, 0, 0.9),
    inset -0.14em 0 0.28em rgba(0, 0, 0, 0.9),
    0 0.07em 0 rgba(255, 255, 255, 0.2);
}

.departure-board span.letter:before {
  border-top: 0.07em solid rgba(0, 0, 0, 0.4);
  border-bottom: 0.07em solid rgba(255, 255, 255, 0.08);
  height: 0;
  position: relative;
  width: 100%;
  left: 0;
  top: 0.62em;
  content: " ";
  display: block;
  z-index: 2;
  -moz-transform: translate(0, -0.05em);
  -o-transform: translate(0, -0.1em);
}

.departure-board span.fold {
  display: block;
  position: absolute;
  height: 0;
  top: 0.65em;
}

.departure-board span.flap {
  display: block;
  position: absolute;
  top: 0em;
  width: 1em;
  height: 0.65em;
  margin: 0;
  overflow: hidden;
}

.departure-board span.text {
  width: 100%;
}

.departure-board span.bottom {
  top: 0.65em;
}

.departure-board span.bottom span.text {
  position: relative;
  top: -0.65em;
}

.departure-board span.flap.falling {
  display: none;
  bottom: 0;
  top: auto;
}

.departure-board span.flap.falling span.text {
  border-top: 0.03em solid #444;
  border-bottom: 0.03em solid #444;
  background: #000;
  display: block;
  position: relative;
  -webkit-transform: scaleY(1);
  transform: scaleY(1);
  transition: -webkit-transform linear;
  transition: transform linear;
  transition: transform linear, -webkit-transform linear;
}

/* Modal */

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.modal-title {
  font-size: 1.8em;
  margin: 2% 0 10px 5%;
}

.modal-text {
  margin: 25px 0;
  text-indent: 45px;
}

.modal-language {
  font-size: 1.2em;
  font-weight: bold;
}

.modal-skills {
  margin: 0 !important;
}

